:root {
    --dark-blue: #1d3557;
    --ultra-light-blue: #f1faee;
    --strong-pink: #e63946;
    font-family: 'Electrolize', sans-serif;
}


.increase-font {
    font-size: 1.1em;
}


.score-window {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
}

.score-table {
    width: 100%;
    height: 100vh;
    max-width: 500px;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: var(--dark-blue)
    
}

.score-table-info {
    margin-top: 5em;
    width: 50%;
    background-color: var(--strong-pink);
    display: flex;
    flex-direction: column;
    
}


.inner-score {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-bottom: 7em;
    color: var(--ultra-light-blue);
}

.player-name {
    background-color: var(--ultra-light-blue);
    text-align: center;
    
    width: 4em;
    color: var(--strong-pink);
    position: relative;
    left: 3.2em;
    bottom: .6em;
}