:root {
    --dark-blue: #1d3557;
    --ultra-light-blue: #f1faee;
    --strong-pink: #e63946;
}

.rounded {
    border-radius: 5px;
}

.main-window {
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: 'Electrolize', sans-serif;
}

.trivia-questions {
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    background-color: var(--ultra-light-blue)

}

.question {
    padding: 0 0.1em;
}

.trivia-questions > .top {
    display: flex;
    justify-content: space-evenly;
    background-color: var(--dark-blue);
    padding-bottom: 5em;
    color: var(--ultra-light-blue)
}

.trivia-questions > .top > .question-box {

    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    position: absolute;
    height: 100px;
    width: 250px;
    background-color: var(--ultra-light-blue);
    border: 1px solid var(--strong-pink) ; 
    margin-top: 5em;
    color: var(--strong-pink)
}

.bot-answers {
    margin-top: 7em;
    display: flex;
    justify-content: space-evenly;

}

.bot-answers > button {
    font-size: 1em;

    font-family: 'Electrolize', sans-serif;
    background-color: var(--dark-blue);
    color: var(--ultra-light-blue);
    border: none;
    height: 3em;
    width: 8em;
    box-shadow: 3px 3px 3px 1px darkgray;

}
