.start-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    font-family: 'Electrolize', sans-serif;
    background-color: #1d3557;

}

.inner-start-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 8em;
}

.start-button {
    margin-top: 3em;
    border: solid 1px #e63946;
    border-radius: 3px;
    padding: 1em 2em;
    font-family: 'Electrolize', sans-serif;
    background-color: #f1faee;
    color: #e63946;
    font-size: 1.1em;

}

h2 {
    padding: 0;
    margin: 0;
    color: #f1faee;
}

